export default class CheckTextInputs {
  constructor(selector) {
    this.txtInputs = document.querySelectorAll(selector);
  }

  textInput(){
    this.txtInputs.forEach(input => {
    input.addEventListener('keypress', function(e) {
      if (e.key.match(/[a-z]/i)) {
        e.preventDefault();
      }
    });
  });
  }
}